
body{
    margin: 0;
}

.top{
  background: hsl(225 , 6%, 25%);
}

.half{
    height: 50vh;
    display: flex;
}

 .head {
    padding: .5rem .5rem .5rem 1rem;
    color:white;
    display: flex;
    background: #121212;
    justify-content: space-between;
    align-items: center;
    border-radius:10px;
}

.editor{
    flex-grow:1;
    flex-basis:0;
    padding: .3rem;
    background-color:hsl(225 , 6%, 25%);
    display:flex;
    flex-direction:column;
    border-radius: 10px;
}

.CodeMirror{
  height:100% !important;
}

.wrapper{
  border-radius:10px;
  flex-grow:1;
  overflow:hidden;
}

.customize{
  border: 5px solid purple;
  width: 30vw;
  padding: 10px;
}

label{
  padding: 5px;
  margin: 5px;
}

select{
  margin:5px;
}

.output-wiindow{
  width:70vw;
}
